<template>
  <b-container fluid class="h-100 d-flex flex-column">
    <b-row class="flex-grow-1">
      <b-col md="4" fluid="md" class="vh-100">
  <b-card class="h-100" no-body>
    <b-card-header header-tag="nav">
      <b-row>
        <b-col>
          <b-form-radio-group
        v-model="mode_selected"
        :options="mode_options"
        buttons
        button-variant="outline-primary"
        size="md"
      ></b-form-radio-group>
        </b-col>
      <b-col>
      <h3 v-if="gridFilter!=''"><b-badge variant="primary">{{gridFilter}} <b-button pill size="sm" @click="gridFilter=''">x</b-button></b-badge></h3>
      </b-col>
       </b-row>
       <b-row>
        <b-col>
           <b-form-select v-model="filter_selected" :options="filter_options"></b-form-select>

    </b-col>
    </b-row>
    <b-row v-if="gridFilter!=''">
    <b-col>Filter: </b-col>
     <b-col> <b-form-checkbox 
      id="checkbox-lost"
        v-model="checkbox_lost"
      >Lost</b-form-checkbox></b-col>
       <b-col> <b-form-checkbox
      id="checkbox-kept"
        v-model="checkbox_kept"
      >Kept</b-form-checkbox></b-col>
       <b-col> <b-form-checkbox
      id="checkbox-gained"
        v-model="checkbox_gained"
      >Gained</b-form-checkbox></b-col>
    </b-row>
    </b-card-header>
<b-card-body>
    <b-list-group class="h-100">
    <b-list-group-item v-for="i,u in displayList" v-if="mode_selected=='Grid'" class="d-flex btn-sm py-1 px-2">
    {{u+1}}. <b>{{i.CommonName}}</b>
    
    <div v-if="gridFilter==''" class="bar kept" v-b-tooltip.right.hover.html="'<b>Lost:</b> '+i.lost+'<br><b>Kept:</b> '+i.kept+'<br><b>Gained:</b> '+i.gained">
      <div class="bar-left lost" :style="{width: i.lost/(i.lost+i.kept+i.gained)*100+'px'}"></div>
      <div class="bar-right gained" :style="{width: i.gained/(i.lost+i.kept+i.gained)*100+'px'}"></div>
    </div>
    <div v-else class="box box-sm" :class="{ kept: i.cat=='kept', gained: i.cat=='gained', lost: i.cat=='lost' }"></div> 
    </b-list-group-item>
      </b-list-group>

</b-card-body>
      </b-card>
      </b-col>
      <b-col class="flex-grow-1">
      <l-map
        :bounds="bounds"
        ref="map">
        <l-tile-layer
          v-for="tileProvider in tileProviders"
          :key="tileProvider.name"
          :name="tileProvider.name"
          :visible="tileProvider.visible"
          :url="tileProvider.url"
          :attribution="tileProvider.attribution"
          layer-type="base"
        />
        <l-control-layers/>

        <l-geo-json 
        :geojson="grid_target" :options-style="styleFunction" 
        :options="options"
        @click="clickGeoJsonFunction"
        ></l-geo-json>
      </l-map>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";

import "leaflet/dist/leaflet.css";
import "leaflet-defaulticon-compatibility/dist/leaflet-defaulticon-compatibility.webpack.css";
import "leaflet.markercluster/dist/MarkerCluster.css";
import "leaflet.markercluster/dist/MarkerCluster.Default.css";

const RdYlGn=["#a50026","#d73027","#f46d43","#fdae61","#fee08b","#ffffbf","#d9ef8b","#a6d96a","#66bd63","#1a9850","#006837"];

import { latLngBounds } from "leaflet";
import {
  LMap,
  LTileLayer,
  LControlLayers,
  LControl,
  LControlZoom,
  LMarker,
  LPopup,
  LIcon,
  LCircle,
  LCircleMarker,
  LGeoJson,
} from "vue2-leaflet";
import Vue2LeafletMarkerCluster from "vue2-leaflet-markercluster";

import "leaflet-defaulticon-compatibility/";

import chroma from "chroma-js"

import grid_target0 from "./assets/grid_target.json"
import sp_old0 from "./assets/sp_old.json"

var grid_target = grid_target0;
grid_target.features = grid_target0.features.map(x => {
  x.properties.SEQ_new = Array.isArray(x.properties.SEQ_new) ? x.properties.SEQ_new : [x.properties.SEQ_new];
  x.properties.SEQ_old = Array.isArray(x.properties.SEQ_old) ? x.properties.SEQ_old : [x.properties.SEQ_old];

  var old_unique = x.properties.SEQ_old.filter(y => !x.properties.SEQ_new.includes(y))

    var new_unique = x.properties.SEQ_new.filter(y => !x.properties.SEQ_old.includes(y))

    x.properties.gained = new_unique.length
    x.properties.kept = x.properties.SEQ_new.length - new_unique.length
    x.properties.lost = old_unique.length
    x.properties.diff = x.properties.gained-x.properties.lost
    x.properties.Sq = x.properties.SqN+x.properties.SqL
    return x
  })

var sp_old = sp_old0.map(sp => {
  sp.lost=0;
  sp.gained=0;
  sp.kept=0;
  grid_target.features.forEach(x => {
    var id_new = x.properties.SEQ_new.includes(sp.SEQ) 
    var id_old = x.properties.SEQ_old.includes(sp.SEQ)
    if (id_new & id_old){
      sp.kept=sp.kept+1
    } else if (id_new & !id_old){
      sp.gained=sp.gained+1
    } else if (!id_new & id_old){
      sp.lost=sp.lost+1
    }
  })
  sp.diff=sp.gained-sp.lost
  
  return sp
})


export default {
  components: {
    LMap,
    LTileLayer,
    LControlLayers,
    LControl,
    LControlZoom,
    LMarker,
    LPopup,
    LIcon,
    LCircle,
    LCircleMarker,
    LGeoJson,
    "v-marker-cluster": Vue2LeafletMarkerCluster,
  },
  data() {
    return {
      mode_options:["Grid", "Species"],
      mode_selected: "Grid",
      checkbox_lost:true,
      checkbox_kept:true,
      checkbox_gained:true,
      filter_options:["Taxonomy", "Lost", "Gained", "Kept","Gained-Lost"],
      filter_selected:"Taxonomy",
      gridFilter:"",
      bounds: latLngBounds([
        [5.615985, 43.50585],
        [-5.353521, 32.958984],
      ]),
      grid_target: grid_target,
      tileProviders: [
        {
          name: "Mapbox.Streets",
          visible: true,
          url: "https://api.mapbox.com/styles/v1/mapbox/streets-v9/tiles/{z}/{x}/{y}?access_token=pk.eyJ1IjoicmFmbnVzcyIsImEiOiIzMVE1dnc0In0.3FNMKIlQ_afYktqki-6m0g",
          attribution: "",
        },
        {
          name: "Mapbox.Satellite",
          visible: false,
          url: "https://api.mapbox.com/styles/v1/mapbox/satellite-streets-v9/tiles/{z}/{x}/{y}?access_token=pk.eyJ1IjoicmFmbnVzcyIsImEiOiIzMVE1dnc0In0.3FNMKIlQ_afYktqki-6m0g",
          attribution: "",
        },
        {
          name: "OpenStreetMap",
          visible: false,
          attribution:
            '&copy; <a target="_blank" href="http://osm.org/copyright">OpenStreetMap</a> contributors',
          url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
        },
        {
          name: "Esri.WorldImagery",
          visible: false,
          url: "https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}",
          attribution:
            "Tiles &copy; Esri &mdash; Source: Esri, i-cubed, USDA, USGS, AEX, GeoEye, Getmapping, Aerogrid, IGN, IGP, UPR-EGP, and the GIS User Community",
        },
      ],
    };
  },
  methods: {
  },
  computed: {
    options() {
      return {
        onEachFeature: (feature, layer) => {
          let prop = feature.properties
        layer.bindTooltip(
          "<b>grid:</b> " + prop.Sq + "<br>" +
          "<b>lost:</b> " + prop.lost + "<br>"+
          "<b>kept:</b> " + prop.kept + "<br>"+
          "<b>gained:</b> " + prop.gained + "<br>"+
          "<b>difference:</b> " + prop.diff,
          { permanent: false, sticky: true }
        );
      }
      }
    },
    displayList(){
      let sp_old_tmp = sp_old
      if (this.gridFilter!=""){
        let f = this.grid_target.features.filter(y =>{
          return y.properties.Sq == this.gridFilter
        })
        if (f.length==1){
         // console.log(f[0])
        sp_old_tmp = sp_old_tmp.map( (y) => {
           var n = f[0].properties.SEQ_new.includes(y.SEQ)
           let o = f[0].properties.SEQ_old.includes(y.SEQ)
           y.cat = (n & o) ? "kept" : (n ? "gained" : ( o ? "lost": ""))
           return y
        })
        //console.log(sp_old_tmp)
        
        sp_old_tmp = sp_old_tmp.filter( (y) => {
          if (y.cat=="lost"){
            return this.checkbox_lost
          } else if (y.cat=="kept"){
            return this.checkbox_kept
          } else if (y.cat=="gained"){
            return this.checkbox_gained
          } else {
            return false
          }
        })
        }

      }
      if (this.filter_selected=="Taxonomy"){
        sp_old_tmp = sp_old_tmp
      } else if (this.filter_selected=="Lost"){
        sp_old_tmp = sp_old_tmp.sort((a,b) => b.lost- a.lost);
      } else if (this.filter_selected=="Gained"){
        sp_old_tmp = sp_old_tmp.sort((a,b) => b.gained - a.gained);
      } else if (this.filter_selected=="Kept"){
        sp_old_tmp = sp_old_tmp.sort((a,b) => b.kept - a.kept);
      } else if (this.filter_selected=="Gained-Lost"){
        sp_old_tmp = sp_old_tmp.sort((a,b) => b.diff - a.diff);
      } 
      return sp_old_tmp
    },
    colorscale(){
      var min = this.grid_target.features.reduce( (acc,x) =>  Math.min(x.properties.diff,acc),10000)
      var max = this.grid_target.features.reduce( (acc,x) =>  Math.max(x.properties.diff,acc),-10000)
      let col = chroma.scale('RdYlGn').domain([min, max]);
      if (this.gridFilter!=""){
        col = col.darken()
      }
      return col
    },
    styleFunction() {
      return (feature, layer) => {
        //console.log(c(1))
        return {
          weight: 2,
          color: "#ECEFF1",
          opacity: 1,
          fillColor: this.colorscale(feature.properties.diff),
          fillOpacity: 1
        };
      };
    },
    clickGeoJsonFunction(){
        return (e) => {
          let Sq = e.sourceTarget.feature.properties.Sq
          this.gridFilter = this.gridFilter==Sq ? "" : Sq
      };
    },
  },
  mounted() {
  },
  created() {
  }
};
</script>
<style>
html,
body {
  height: 100%;
  margin: 0;
}
.card-body{
overflow-y: scroll;
}
.box-legend {
   width: 20px;
   height: 20px;
   margin: 5px;
   border: 1px solid rgba(0, 0, 0, .2);
}
.box-sm {
   width: 0.875rem;
   height: 0.875rem;
   margin-left: auto;
   border: 1px solid rgba(0, 0, 0, .2);
}
.bar {
   width: 100px;
   height: 0.875rem;
   margin-left: auto;
   position: relative;
  top: 50%;
  transform: translateY(-50%);
}
.bar-left{
   float: left;
   height: 0.875rem;
}
.bar-right{
   float: right;
   height: 0.875rem;
}
.lost {
   background: #d73027;
}
.kept {
   background: #d9ef8b;
}
.gained {
   background: #1a9850;
}
.custom-checkbox #checkbox-lost:checked ~ .custom-control-label::before {
  background-color: #d73027!important;
}
.custom-checkbox #checkbox-lost ~ .custom-control-label::before{
   border: 1px solid #d73027;
}
.custom-checkbox #checkbox-kept:checked ~ .custom-control-label::before {
  background-color: #d9ef8b!important;
}
.custom-checkbox #checkbox-kept ~ .custom-control-label::before{
   border: 1px solid #d9ef8b;
}
.custom-checkbox #checkbox-gained:checked ~ .custom-control-label::before {
  background-color: #1a9850!important;
}
.custom-checkbox #checkbox-gained ~ .custom-control-label::before{
   border: 1px solid #1a9850;
}

</style>
